@tailwind base;
@tailwind components;
@tailwind utilities;


*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  
    outline: none;
    transition: 0.5s all;
  }
  
  .styletext{
    font-family:Segoe print, Arial, Helvetica, sans-serif;
}
::-webkit-scrollbar{
  width: 4px;
  z-index: 100;
  position: relative;
  background-color: transparent;
}
::-webkit-scrollbar-thumb{
background: rgb(219 39 119);
  border-radius: 20px;
}
 
 img{
  background: transparent;
 }
 .gradient{
  background:linear-gradient(rgb(232, 19, 54),black);
 }
 *{
  overflow-x: hidden;
scroll-behavior: smooth;
}
 .alb_card:hover .play{
display: flex;

 }
 .play{
  display: none;
  transition: 0.5s ease-in;
 }
